import { createStore } from "vuex";
import axios from "axios";
const store = createStore({
    state: {
        apiKey: "ea8274c23d69472828079f18267fc41b",
        weather: null
    },
    mutations: {
        setWeather(state, info) {
            state.weather = info
        }
    },
    actions: {
        async getWeather({ commit, state }, city) {

            let res = await axios(`http://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`)
            let data = res.data[0]
            const { lat, lon, name } = data
            let res2 = await axios(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,alerts&appid=${state.apiKey}&units=metric`)
            let info = res2.data
            
            let weatherInfo = {
                ...info,
                name
            }
            commit("setWeather", weatherInfo)
        }
    },
    getters: {
        getCurrent: state => state.weather.current,
        getDay: state => state.weather.daily.filter((day, i, arr) => i < arr.length -1)
    }
})
export default store
