<template>
   <div class="day">
    <h2 class="day__title">{{ getWeek}}</h2>
    <p class="day__data">{{ getDay }} {{ getMonth }}</p>
    <img v-if="description=='clear sky'" class="day__img" src="../assets/images/animated/day.svg" alt="">
      <img v-else-if="description=='few clouds'" class="day__img" src="../assets/images/animated/cloudy-day-2.svg" alt="">
      <img v-else-if="description=='light rain'" class="day__img" src="../assets/images/animated/rainy-2.svg" alt="">
      <img v-else-if="description=='moderate rain'" class="day__img" src="../assets/images/animated/rainy-5.svg" alt="">
      <img v-else-if="description=='shower rain'" class="day__img" src="../assets/images/animated/rainy-6.svg" alt="">
      <img v-else-if="description=='light snow'" class="day__img" src="../assets/images/animated/snowy-1.svg" alt="">
      <img v-else-if="description=='snow'" class="day__img" src="../assets/images/animated/snowy-5.svg" alt="">
      <img v-else-if="description=='thunderstorm'" class="day__img" src="../assets/images/animated/thunder.svg" alt="">
      <img v-else class="day__img" src="../assets/images/animated/cloudy.svg" alt=""> 
      <p class="day__max">
        {{
            Math.round(day.temp.max) > 0
            ? "+" + Math.round(day.temp.max)
            : Math.tound(day.temp.max)
        }}°
      </p>
      <p class="day__min">
        {{
            Math.round(day.temp.min) > 0
            ? "+" + Math.round(day.temp.min)
            : Math.tound(day.temp.min)
        }}°
      </p>
      <p class="day__descr">{{ description}}</p>
   </div>
</template>
<script>
export default {
    props:["day"],
    computed:{
        description() {
            return this.day.weather[0].description;
        },
        getWeek(){
            const milliSec = this.day.dt *1000
            const date = new Date (milliSec);
            return date.toLocaleString("en-Us",{
                weekday:"long",
            });
        },
        getMonth(){
            const milliSec = this.day.dt *1000
            const date = new Date (milliSec);
            return date.toLocaleString("en-Us",{
                month:"long",
            });
        },
        getDay(){
            const milliSec = this.day.dt *1000
            const date = new Date (milliSec);
            return date.toLocaleString("en-Us",{
                day:"numeric",
            });
        },
    },
};  
</script>
