<template>
  <div class="content" v-if="weather">
    <div class="content__left">
      <p class="content__left-deg">{{Math.round(getCurrent.temp)}}°</p>
      <p class="content__left-day">Today</p>
      <p class="content__left-time">Time: {{ getTime }}</p>
      <p class="content__left-city">City: {{ weather.name }} </p>
      <img v-if="description=='clear sky'" class="content__left-img" src="../assets/images/animated/day.svg" alt="">
      <img v-else-if="description=='few clouds'" class="content__left-img" src="../assets/images/animated/cloudy-day-2.svg" alt="">
      <img v-else-if="description=='light rain'" class="content__left-img" src="../assets/images/animated/rainy-2.svg" alt="">
      <img v-else-if="description=='moderate rain'" class="content__left-img" src="../assets/images/animated/rainy-5.svg" alt="">
      <img v-else-if="description=='shower rain'" class="content__left-img" src="../assets/images/animated/rainy-6.svg" alt="">
      <img v-else-if="description=='light snow'" class="content__left-img" src="../assets/images/animated/snowy-1.svg" alt="">
      <img v-else-if="description=='snow'" class="content__left-img" src="../assets/images/animated/snowy-5.svg" alt="">
      <img v-else-if="description=='thunderstorm'" class="content__left-img" src="../assets/images/animated/thunder.svg" alt="">
      <img v-else class="content__left-img" src="../assets/images/animated/cloudy.svg" alt="">
    </div>
    <div class="content__right">
      <img src="../assets/images/cloud.png" alt="" class="content__right-cloud" />
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/temp.svg" alt="" />
        </div>
        <h3 class="content__info-title">Temperature</h3>
        <p class="content__info-text">{{Math.round(getCurrent.temp)}}° - feels like {{Math.round(getCurrent.feels_like)}}°</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/davleniya.svg" alt="" />
        </div>
        <h3 class="content__info-title">Pressure</h3>
        <p class="content__info-text">{{Math.round(getCurrent.Pressure)}} mm mercury column</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/yomgir.svg" alt="" />
        </div>
        <h3 class="content__info-title">Humidity</h3>
        <p class="content__info-text">{{Math.round(getCurrent.humidity)}} %</p>
      </div>
      <div class="content__info">
        <div class="content__info-img">
          <img src="../assets/images/wind.svg" alt="" />
        </div>
        <h3 class="content__info-title">Wind</h3>
        <p class="content__info-text">{{Math.round(getCurrent.wind_speed)}} m/s southwest</p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapGetters} from 'vuex';
export default {
    computed:{
        ...mapGetters(["getCurrent"]),
        ...mapState(['weather']),
        getTime(){
          return new Date().toLocaleString('en-Us',{
            hourCycle:'h23',
            timeZone:this.weather.timezone,
            timeStyle:'short'
          });
        },
        description(){
          return this.getCurrent.weather[0].description
        }
    },
};
</script>
<style lang=""></style>
