<template>
  <nav class="nav">
    <a href="#!" class="nav__logo">
      <img src="../assets/images/Header_logo.svg" alt="" />
      Weather
    </a>
    <input
      type="text"
      placeholder="Choose City"
      class="nav__input"
      v-model="city"
      @keydown.enter="getWeather(city) , city=''"
    />
  </nav>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      city: "",
    };
  },
  methods: {
    ...mapActions(["getWeather"]),
  },
  created() {
    this.getWeather("Tashkent");
  },
};
</script>
<style lang=""></style>
