<template>
<div class="week" v-if="weather">
    <Day v-for="day in getDay" :key="day" :day="day" />
</div>
</template>
<script>
import { mapGetters, mapState } from 'vuex';
import Day from './Day.vue';

export default {
    components: { Day },
    computed: {
        ...mapGetters(["getDay"]),
        ...mapState(["weather"]),
    },
};
</script>